/* 페이지 이동 시 렌더링 애니메이션 */
.render {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: render-animation 1s;
  z-index: 999;
  opacity: 0;

  @keyframes render-animation {
    0% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}
